import { useContext } from "react";
import { useTransition, animated } from "react-spring";

import AppContext from "../../context";
import styles from "./Popup.module.scss";

function Popup() {
  const { modal, setModal, setStep, setFaqScreen } = useContext(AppContext);

  const transition = useTransition(!modal, {
    from: { opacity: 0, transform: "translateY(100%)" },
    enter: { opacity: 1, transform: "translateY(0)" },
  });

  return (
    <>
      {transition(
        (style, item) =>
          item && (
            <animated.div style={style} className={styles.widgetFixed}>
              <div className={styles.widgetFixedOffSeason}>
                <div>
                  <div className={styles.widgetFixedTitle}>
                    Окончание сезона!
                  </div>
                  Старт нового сезона с <span>01 Май 2022 г.</span>
                </div>
                <div>
                  <div className={styles.widgetFixedTitle}>
                    Сейчас возможно:
                  </div>
                  <ol>
                    <li>Заранее забронировать место</li>
                    <li>
                      Купить подарочный сертификат с повышенным сроком действия
                    </li>
                  </ol>
                </div>
              </div>
              <div className={styles.widgetFixedInner}>
                <div className={styles.widgetFixedBox}>
                  <button
                    className={`${styles.widgetFixedButton} ${styles.widgetFixedButtonStyled}`}
                    onClick={() => {
                      setModal(!modal);
                      setStep("selfTimes");
                    }}
                  >
                    Забронировать время
                  </button>
                  <button
                    className={styles.widgetFixedButton}
                    onClick={() => {
                      setModal(!modal);
                      setStep("giftCertificateOrder");
                    }}
                  >
                    Подарочный сертификат
                  </button>
                  <button
                    className={styles.widgetFixedButton}
                    onClick={() => {
                      setModal(!modal);
                      setFaqScreen(true);
                    }}
                  >
                    Вопросы и ответы
                  </button>
                </div>
              </div>
            </animated.div>
          )
      )}
    </>
  );
}

export default Popup;
