import { useContext } from "react";
import { useTransition, animated } from "react-spring";

import AppContext from "../../context";
import Top from "./widgetModalTop";
import Bottom from "./widgetModalBottom";
import styles from "./Modal.module.scss";

import Times from "./popupContent/times";
import Programs from "./popupContent/programs";
import MapComponent from "./popupContent/map";
import Variants from "./popupContent/variants";
import Contact from "./popupContent/contact";
import Rules from "./popupContent/rules";
import CertificateOrder from "./popupContent/certificateOrder";
import Congratulation from "./popupContent/сongratulation";
import FAQ from "./popupContent/faq";

function Modal() {
  const { modal, step, setStep, certificateDesign, faqScreen } =
    useContext(AppContext);
  const transition = useTransition(modal, {
    from: { opacity: 0, transform: "translateY(-100%)" },
    enter: { opacity: 1, transform: "translateY(0)" },
  });

  return (
    <>
      {transition(
        (style, item) =>
          item && (
            <animated.div style={style} className={styles.widgetModal}>
              <div className={styles.widgetModalInner}>
                <div className={styles.widgetModalFrame}>
                  <form
                    onSubmit={(e) => {
                      e.preventDefault();
                      if (step === "selfContact") {
                        setStep("selfRules");
                      }
                    }}
                  >
                    <div className={styles.popupInner}>
                      <Top />
                      <div className={styles.popupContent}>
                        {faqScreen ? (
                          <FAQ />
                        ) : (
                          <>
                            {step === "selfTimes" && <Times />}
                            {step === "selfPrograms" && <Programs />}
                            {step === "selfMap" && <MapComponent />}
                            {step === "selfVariants" && <Variants />}
                            {step === "selfContact" && <Contact />}
                            {step === "selfRules" && <Rules />}
                            {step === "giftCertificateOrder" && (
                              <CertificateOrder />
                            )}
                            {step === "giftPrograms" && <Programs />}
                            {step === "giftVariants" && <Variants />}
                            {step === "giftCongratulation" && (
                              <Congratulation />
                            )}
                            {step === "giftContact" && <Contact />}
                          </>
                        )}
                      </div>
                      {!faqScreen && !certificateDesign && (
                        <Bottom step={step} />
                      )}
                    </div>
                  </form>
                </div>
              </div>
            </animated.div>
          )
      )}
    </>
  );
}

export default Modal;
