import { useState } from "react";
import { useTransition, animated } from "react-spring";
import Slider from "react-slick";

function Content({ styles, item }) {
  const [textShow, setTextShow] = useState(false);
  const [picturesShow, setPicturesShow] = useState(false);

  const text = useTransition(textShow, {
    from: { opacity: 0, transform: "translateX(-100%)" },
    enter: { opacity: 1, transform: "translateX(0)" },
  });

  function SampleNextArrow(props) {
    const { className, onClick } = props;
    return (
      <button
        className={`${className} ${styles.slickNext}`}
        onClick={onClick}
      />
    );
  }

  function SamplePrevArrow(props) {
    const { className, onClick } = props;
    return (
      <button
        className={`${className} ${styles.slickPrev}`}
        onClick={onClick}
      />
    );
  }

  const settings = {
    lazyLoad: true,
    infinite: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    variableWidth: true,
    initialSlide: 2,
    nextArrow: <SampleNextArrow />,
    prevArrow: <SamplePrevArrow />,
  };

  return (
    <div style={{ overflow: "hidden" }}>
      {text(
        (style, obj) =>
          obj && (
            <animated.div style={style} className={styles.detailText}>
              <div>
                <p>
                  <b>Доступно с</b> {item.availableFrom} лет
                </p>
                <p>
                  <b>Пассажиры с</b> {item.passengerFrom} лет
                </p>
                <p>
                  <b>Ограничения:</b> {item.restrictions}
                </p>
                <p>
                  <b>Одежда:</b> {item.clothes}
                </p>
                <p>
                  <b>Выдается на месте:</b> {item.issuedLocally}
                </p>
              </div>
              <div style={{ whiteSpace: "pre-wrap" }}>{item.text}</div>
            </animated.div>
          )
      )}
      {picturesShow && (
        <div className={styles.pictures}>
          <Slider {...settings}>
            {item.video.map((obj) => {
              return (
                <video
                  key={obj.src}
                  preload="auto"
                  autoPlay
                  muted
                  controls
                  playsInline
                >
                  <source src={obj.src} type="video/mp4"></source>
                </video>
              );
            })}
            {item.images.map((obj) => {
              return <img key={obj.src} src={obj.src} alt="img"></img>;
            })}
          </Slider>
        </div>
      )}
      <div className={styles.programDetailBtns}>
        <button
          type="button"
          className={`${styles.programBtn} ${styles.programDetailBtn}`}
          onClick={() => {
            setPicturesShow(false);
            setTextShow(!textShow);
          }}
        >
          Подробнее о маршруте
        </button>
        <button
          type="button"
          className={`${styles.programBtn} ${styles.programDetailBtn}`}
          onClick={() => {
            setTextShow(false);
            setPicturesShow(!picturesShow);
          }}
        >
          Фото и видео
        </button>
      </div>
    </div>
  );
}
export default Content;
